
@import url("../Styles//fonts.css");

@keyframes fadein {
    from {opacity: 0;}
    to {opacity: 2;}
    }

.container{
 
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;
   
}
.social_media{
    position: fixed;
    height: 90vh;
    width: 5%;
    background-color: transparent;
    display: flex;
    flex-direction: column;
   
    justify-content: center;
    align-items: center;
    min-width: 80px;
}

.social_media_fakeContainer{
    height: 90vh;
    width: 5%;
    background-color: transparent;
    display: flex;
    flex-direction: column;
    min-width: 80px;
    justify-content: center;
    align-items: center;
}



.ul{
    margin: 0px;
    padding: 0px;
    list-style: none;
    list-style-type: none;
   
    background-color: transparent;
    display: flex;
    flex-direction: column;
    gap: 22px;
    justify-content: center;
    align-items: center;
    width: 100%;
   
}
.svg{
 
}

.github{
    // width: 20vw;
    height: 5vh;
    // margin: 0px;
    // padding: 0px;
}

.insta{
    // width: 20vw;
    height: 5vh;
    margin: 0px;
    padding: 0px;
}
.linkdedin{
    // width: 20vw;
    height: 4vh;
    margin: 0px;
    padding: 0px;
}
.mail{
    // width: 20vw;
    height: 4.2vh;
    margin: 0px;
    padding: 0px;
}

.home_container{
    position: static;
    margin-top: 10vh;
    margin-bottom: 10vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  
}

.left_section{
    margin-left: 5%;
    background-color: transparent;
    width: 45%;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: start;
    z-index: 10;
}

.row_one{
    background-color: transparent;
    height: auto;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

}

.row_two{
    background-color: transparent;
    height: auto;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

}

.learn_more_button{
    width: 20%;
    height: 50px;
    width: 200px;
    border-radius: 10px;
    border-color: transparent;
    background: #FFF;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25) inset;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
    text-decoration: none;
    color: #7E7E7E;
}
.Hey{
    color: black;
    font-family: indieFlower;
    margin: 0px;
    font-size: calc(10px + 1vw);
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    animation: fadein 2s forwards  ;
    opacity: 0;
}

.MyName{
    margin: 0px;
    margin-top: -10px;
    font-family: indieFlower;
    font-size: 90px;
    font-size: calc(12px + 2vw);
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    background: linear-gradient(90deg, #FF02B8 8.45%, #050FFF 46.87%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: fadein 1s forwards  ;
    opacity: 0;
}

.DesignLine{
    text-align:center ;
    margin: 0px;
    margin-top: -10px;
    font-family: indieFlower;
    font-size: 90px;
    font-size: calc(12px + 2vw);
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    background: linear-gradient(90deg, #FF02B8 8.45%, #050FFF 46.87%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: fadein 2s forwards  ;
    opacity: 0;
}



.right_section{
    background-color: transparent;
    width: 45%;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: start;
    background-color: transparent;
    
    

}

.profile_photo{
    width: 60%;
    z-index: 6;
    max-width: 500px;
    min-width: 400px;
    border-radius: 250px;
    padding: 0.4px;
    background-color: #d102ff;
    animation: fadein 1s forwards  ;
    opacity: 0;

}

.paragraph_container{
    background-color: #e5e5e55a;
    width: 100%;
    padding: 50px;
    border-radius: 20px;
    // margin: 20px;
    margin-top: 40px;
    margin-bottom: 40px;

    backdrop-filter: blur(10px); /* Adjust the blur value as needed */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    animation: fadein 1s forwards  ;
    opacity: 0;
}
.outro{
    width: 70%;
    font-family: indieFlower;
    text-align: center;
    font-size: calc(4px + 1vw);

}

@media only screen and (max-width: 1100px) {
    .home_container {
        margin-top: 10px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    }
    .row_one{
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        
    }

    .row_two{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .left_section{
        width: 90%;
        margin-left: 0%;
        height: auto;
        justify-content: center;
        align-items: center;
    }
    .right_section{
        width: 90%;
        height: auto;
         justify-content: center;
         align-items: center;
    }

    .profile_photo{
        width: 90%;
        min-width: 200px;
        
 
    }

    .Hey{

        font-size: calc(13px + 1vw);
    }

    .MyName{
   
        font-size: calc(18px + 2vw);
    }

    .DesignLine{
        font-size: calc(18px + 2vw); 
    }

    .container{
        flex-direction: column-reverse;
        background-color: transparent;
    }
    .social_media{
        width: 100%;
        height: auto;
        flex-direction: row;
        background-color: transparent;
        position: relative;
        top:  25px;
        margin-bottom: 50px;
    }
    .social_media_fakeContainer{
       background-color: transparent;
        width: 100%;
        height: auto;
        
    }
    .ul{
        width: 50%;
        background-color: transparent;
       gap: 0;
     
        flex-direction: row;
      
        align-items: center;
       
        
    }

    .github{
    
        height: 5vh;
      
    }
    
    .insta{
        width: 20vw;
       
    }
    .linkdedin{
        width: 20vw;
      
    }
    .mail{
        width: 20vw;
     
     
    }

    .outro{
        width: 80%;
        font-family: indieFlower;
        text-align: center;
        font-size: calc(7px + 2vw);
    
    }
   
    }
